import React, { useState, useEffect } from "react";
import { BrowserRouter as Router } from "react-router-dom";
import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles";
import Header from "./layouts/Header.js";
import Page from "./layouts/Page";
import Footer from "./layouts/Footer";
import "./styles/App.css";

const theme = createMuiTheme({
  palette: {
    primary: {
      main: "#67a509"
    },
    secondary: {
      main: "#0277bd"
    }
  }
});

function App() {
  const basketData = [
    {
      id: 1,
      title: "Fjallraven - Foldsack No. 1 Backpack, Fits 15 Laptops",
      price: 109.95,
      description:
        "Your perfect pack for everyday use and walks in the forest. Stash your laptop (up to 15 inches) in the padded sleeve, your everyday",
      category: "men clothing",
      image: "https://fakestoreapi.com/img/81fPKd-2AYL._AC_SL1500_.jpg",
      quantity: 2
    },
    {
      id: 2,
      title: "Mens Casual Premium Slim Fit T-Shirts ",
      price: 22.3,
      description:
        "Slim-fitting style, contrast raglan long sleeve, three-button henley placket, light weight & soft fabric for breathable and comfortable wearing. And Solid stitched shirts with round neck made for durability and a great fit for casual fashion wear and diehard baseball fans. The Henley style round neckline includes a three-button placket.",
      category: "men clothing",
      image:
        "https://fakestoreapi.com/img/71-3HjGNDUL._AC_SY879._SX._UX._SY._UY_.jpg",
      quantity: 3
    },
    {
      id: 3,
      title: "Mens Cotton Jacket",
      price: 55.99,
      description:
        "great outerwear jackets for Spring/Autumn/Winter, suitable for many occasions, such as working, hiking, camping, mountain/rock climbing, cycling, traveling or other outdoors. Good gift choice for you or your family member. A warm hearted love to Father, husband or son in this thanksgiving or Christmas Day.",
      category: "men clothing",
      image: "https://fakestoreapi.com/img/71li-ujtlUL._AC_UX679_.jpg",
      quantity: 1
    },
    {
      id: 4,
      title: "Mens Casual Slim Fit",
      price: 15.99,
      description:
        "The color could be slightly different between on the screen and in practice. / Please note that body builds vary by person, therefore, detailed size information should be reviewed below on the product description.",
      category: "men clothing",
      image: "https://fakestoreapi.com/img/71YXzeOuslL._AC_UY879_.jpg",
      quantity: 5
    }
  ];

  const [productsList, setProductsList] = useState("");
  const [basket, setBasket] = useState(basketData);

  const chanProdQuantInBask = (productObj, newQuantity) => {
    const updProdObj = basket.map((el) => {
      if (el.id === productObj.id) {
        el.quantity = newQuantity;
        return { ...el, "el.quantity": newQuantity };
      } else return el;
    });
    setBasket(updProdObj);
  };

  useEffect(() => {
    const API = "https://fakestoreapi.com/products?limit=999";
    fetch(API)
      .then((response) => {
        if (response.ok) return response;
        else throw Error(response.statusText);
      })
      .then((response) => response.json())
      .then((response) => setProductsList(response))
      .catch((errors) => console.log(errors));
  }, []);

  return (
    <ThemeProvider theme={theme}>
      <Router basename={process.env.PUBLIC_URL}>
        <div className="App">
          <Header basket={basket} />
          <content>
            {/* Can be horisontal Navbar here in the future */}
            <Page
              productsList={productsList}
              basket={basket}
              setBasket={setBasket}
              chanProdQuantInBask={chanProdQuantInBask}
            />
          </content>
          <footer>
            <Footer />
          </footer>
        </div>
      </Router>
    </ThemeProvider>
  );
}

export default App;
